import React, { useState, useEffect } from "react";
import axios from "axios";
import { Table } from "react-bootstrap";  // استخدم Bootstrap لعرض الجدول

const ManageUsers = () => {
  const [users, setUsers] = useState([]);

  // استخدم useEffect لاسترجاع البيانات عند تحميل الصفحة
  useEffect(() => {
    // استعلام إلى Spring Boot API
    axios
      .get("http://localhost:8080/api/users")  // التأكد من المسار الصحيح
      .then((response) => {
        setUsers(response.data);  // حفظ البيانات في الحالة
      })
      .catch((error) => {
        console.error("Error fetching users:", error);
      });
  }, []);  // تشغيله مرة واحدة عند تحميل المكون

  return (
    <div className="container">
      <h2>Manage Users</h2>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>ID</th>
            <th>Username</th>
            <th>Email</th>
            <th>Role</th>
          </tr>
        </thead>
        <tbody>
          {users.length === 0 ? (
            <tr>
              <td colSpan="4">No users found</td>
            </tr>
          ) : (
            users.map((user) => (
              <tr key={user.id}>
                <td>{user.id}</td>
                <td>{user.username}</td>
                <td>{user.email}</td>
                <td>{user.role}</td>
              </tr>
            ))
          )}
        </tbody>
      </Table>
    </div>
  );
};

export default ManageUsers;
